<template>
    <div class="logout">
        <a-row class="mt-60">
            <a-col :span="12" :offset="6">
                <h3>You have safely logged out!</h3>
                <h6>Jump to the home page after {{m}} seconds</h6>
            </a-col>
        </a-row>
        <div style="margin-top: 500px;"></div>
    </div>
</template>


<script>
export default {
    name: "Logout",
    components: {},
    data() {
        return {
            loading: false,
            m: 5,
            tempInterval: 0,
        };
    },
    beforeCreate() {},
    beforeDestroy() {
        clearInterval(this.tempInterval);
    },
    methods: {
        init() {
            this.g.userInfo = null;
            this.$EventBus.$emit("logoutComplete");

            this.tempInterval = setInterval(() => {
                this.m -= 1;
                if (this.m <= 0) {
                    clearInterval(this.tempInterval);
                    this.$router.push("/");
                }
            }, 1000);
        },
    },
    mounted() {
        this.init();
    },
};
</script>

